import { BiPluginConfig } from '../../types';
import Sidebar from './components/Sidebar';
import {
  SizeAndStyle,
  RecommendStyle,
} from './components/SidebarMenus';

function sidebar(config: BiPluginConfig) {
  const { image = {} } = config;
  const { locale = {} } = image;
  return {
    imageOptions: Sidebar,
    items: {
      sizeAndStyle: SizeAndStyle,
      recommendStyle: RecommendStyle,
    },
    config: image,
    locale,
  };
}

export default sidebar;
